.wrap {
  padding-bottom: 20px;
  padding-top: 20px;
  margin: 0 auto;
  display: inline-block;
  // transform: translateX(-50%);
  width: 100%;
}

.content {
  display: flex;
  font-family: 'Roboto';
  color: #081540;
  font-size: 16px;
  font-weight: 600;
  width: max-content;
  margin: 0 auto;

  p {
    margin-left: 7px;
  }

  align-items: center;
}

.heart {
  margin-left: 7px;
  width: 16px;
  height: 16px;

  p {
    margin: 0;
    line-height: 1.2;
  }
}

.haloLab {
  margin-left: 7px;
}

.logo {
  width: 30px;
  height: 30px;
  margin-left: 7px;
}

.content:hover .logo path:first-child {
  fill: #ffe30d;
}
